.events-card {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;

  @media screen and (min-width: 1280px) {
    margin: auto;
    width: 946px;
    padding-top: 387px;
    padding-right: 70px;
  }
  @media screen and (min-width: 768px) and (max-width: 1279px) {
    width: 610px;
    padding-top: 304px;
    padding-right: 27px;
  }
  @media screen and (max-width: 767px) {
    width: 280px;
    padding-top: 387px;
    padding-right: 14px;
  }
}
.events-list-item {
  @media screen and (min-width: 1280px) {
    flex-basis: calc(100% / 4);
  }
  @media screen and (min-width: 768px) and (max-width: 1279px) {
    flex-basis: calc(100% / 3);
  }

  @media screen and (max-width: 767px) {
    flex-basis: calc(100% / 2);
  }
}
.event-card-information {
  @media screen and (min-width: 1280px) {
    display: block;
    position: relative;
    z-index: 1;
  }
}
.event-card-information:after {
  display: inline-block;
  content: '';
  border: 1px solid $border-card-color;
  box-sizing: border-box;
  border-radius: 50px 0px;
  position: absolute;
  z-index: -1;

  @media screen and (min-width: 1280px) {
    width: 153px;
    height: 143px;
    margin-left: 40px;
    margin-top: -310px;
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    width: 153px;
    height: 143px;
    border: 1px solid $border-card-color;
    margin-left: 40px;
    margin-top: -310px;
  }

  @media screen and (max-width: 767px) {
    width: 108px;
    height: 101px;
    margin-left: 30px;
    margin-top: -235px;
  }
}

.event-card-photo {
  position: relative;
  z-index: 1;

  @media screen and (min-width: 1280px) {
    padding-bottom: 63px;
  }
  @media screen and (min-width: 768px) and (max-width: 1279px) {
    padding-bottom: 58px;
  }
  @media screen and (max-width: 767px) {
    padding-bottom: 39px;
  }
}

.event-card-img {
  display: block;
  border-radius: 50px 0px;

  @media screen and (min-width: 1280px) {
    width: 180px;
    height: 227px;
    left: 175px;
    top: 400px;
    z-index: 1;
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    width: 180px;
    height: 227px;
    left: 501px;
    top: 317px;
  }

  @media screen and (max-width: 767px) {
    width: 128px;
    height: 161px;
    left: 25px;
    top: 2706px;
  }
}

.event-card-group-name {
  font-family: $primary-font;
  font-style: normal;
  font-weight: bold;
  text-align: center;
  margin-top: 5px;
  margin-bottom: 0px;
  color: $title-color;

  @media screen and (min-width: 1280px) {
    font-size: 16px;
    line-height: 1.25;
  }
  @media screen and (min-width: 768px) and (max-width: 1279px) {
    font-size: 16px;
    line-height: 1.25;
  }
  @media screen and (max-width: 767px) {
    width: 128px;
    font-size: 14px;
    line-height: 1.21;
  }
}

.event-card-date {
  font-family: $primary-font;
  font-style: normal;
  font-weight: normal;
  margin-top: 5px;
  margin-bottom: 0px;
  text-align: center;
  color: $primary-text-color;
  @media screen and (min-width: 1280px) {
    font-size: 16px;
    line-height: 1.25;
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    font-size: 16px;
    line-height: 1.25;
  }

  @media screen and (max-width: 767px) {
    font-size: 13px;
    line-height: 1.3;
  }
}

.event-card-concert-venue {
  font-family: $primary-font;
  font-style: normal;
  font-weight: 600;
  margin-top: 5px;
  margin-bottom: 0px;
  text-align: center;
  color: $primary-text-color;

  @media screen and (min-width: 1280px) {
    font-size: 14px;
    line-height: 1.21;
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    font-size: 14px;
    line-height: 1.21;
  }

  @media screen and (max-width: 767px) {
    font-size: 12px;
    line-height: 1.25;
  }
}

.event-card-icon {
  @media screen and (min-width: 768px) {
    width: 7px;
    height: 10px;
  }
}
.event-card-icon {
  @media screen and (max-width: 767px) {
    width: 6px;
    height: 9px;
  }
}
